.listaHabitaciones {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 15vh;
  padding: 5vh;
  margin-top: -10vh;
  overflow-y: hidden;
  height: 60vh;
  width: 30vw;
  padding: 25vh;
  position: relative;
  left: 30%;
  overflow-x: hidden;
}

.listaHabitaciones:hover {
  overflow-y: scroll; /* Mostrar la barra de desplazamiento solo cuando se aplique :hover */
}

/* Estilos de la barra de desplazamiento */
.listaHabitaciones::-webkit-scrollbar {
  width: 8px; /* Ancho de la barra de desplazamiento */
}

.listaHabitaciones::-webkit-scrollbar-thumb {
  background-color: rgba(
    0,
    0,
    0,
    0.3
  ); /* Color del pulgar de la barra de desplazamiento */
  border-radius: 4px; /* Borde redondeado del pulgar */
}

.listaHabitaciones::-webkit-scrollbar-track {
  background-color: transparent; /* Color de fondo de la barra de desplazamiento */
}
.habitacionConcreta {
  background: rgba(116, 201, 253, 0.2);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(12.5px);
  -webkit-backdrop-filter: blur(12.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  width: 15vw;
  height: 20vh;
  transition: all 0.5s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  position: relative;
  margin: 15vh;
  z-index: 1; /* Asegura que el elemento centrado esté por encima de los demás */
}

.habitacionConcreta:hover {
  transform: scale(1.1);
  cursor: pointer;
  z-index: 2; /* Aumenta el z-index para asegurar que el elemento resaltado esté por encima de los demás en :hover */
}

.habitacionConcreta.outside {
  opacity: 0.9; /* Opacidad reducida para los elementos fuera del centro */
  transform: scale(0.5);
  z-index: -2;
}

.habitacionConcreta.centrado {
  transform: scale(1.5); /* Escala mayor para el elemento centrado */
}
.habitacionConcreta.cerquisima {
  opacity: 0.9; /* Opacidad reducida para los elementos fuera del centro */
  transform: scale(1.5);
  z-index: -2;
}
.habitacionConcreta:hover .eliminarHabitacion {
  display: block; /* Mostrar el botón solo cuando el ratón está encima */
}

h2 {
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  text-align: center;
  cursor: pointer;
}

.eliminarHabitacion {
  display: none; /* Ocultar el botón por defecto */
  background-image: url(../resources/images/eliminar.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  position: absolute;
  top: 1rem;
  right: 1rem;
  border: none;
  cursor: pointer;
}

.cabeceraHabitaciones {
  margin-bottom: 5vh;
  background: rgba(116, 201, 253, 0.2);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(12.5px);
  -webkit-backdrop-filter: blur(12.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  width: 15vw;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.Crear {
  background-image: url(../resources/images/agregar.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 1rem;
  height: 4vh;
  border-radius: 50%;
}

#textoEmergente {
  display: none;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-5px, -50%);
  color: black;
  font-weight: 900;
  padding: 5px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 4px;
  white-space: nowrap;
}

#crear:hover + #textoEmergente {
  display: block;
}
